body {
    padding: 0;
    margin: 0;
    font-size: 14px;
    font-family: "Helvetica Nenu", Hevetica, Arial, sans-serif;
    /*overflow: hidden*/
}

body.ready {
    border: 1px solid #eee;
    border-radius: 5px;
    border-color: #eee #ddd #bbb;
    -webkit-box-shadow: rgba(0, 0, 0, .14) 0 1px 3px;
            box-shadow: rgba(0, 0, 0, .14) 0 1px 3px
}

.github-card {
    border-radius: 5px;
    padding: 8px 8px 0;
    background: #fff;
    color: #555;
    position: relative
}

.github-card a {
    text-decoration: none;
    color: #4183c4;
    outline: 0
}

.github-card a:hover {
    text-decoration: underline
}

.github-card .header {
    position: relative
}

.github-card .button {
    position: absolute;
    top: 0;
    right: 8px;
    padding: 4px 8px 4px 7px;
    color: #555;
    text-shadow: 0 1px 0 #fff;
    border: 1px solid #d4d4d4;
    border-radius: 3px;
    font-size: 13px;
    font-weight: 700;
    line-height: 14px;
    background-color: #e6e6e6;
    background-image: -webkit-linear-gradient(#fafafa, #eaeaea);
    background-image: -webkit-gradient(linear, left top, left bottom, from(#fafafa), to(#eaeaea));
    background-image: -o-linear-gradient(#fafafa, #eaeaea);
    background-image: linear-gradient(#fafafa, #eaeaea)
}

.github-card .button:hover {
    color: #fff;
    text-decoration: none;
    background-color: #3072b3;
    background-image: -webkit-linear-gradient(#599bdc, #3072b3);
    background-image: -webkit-gradient(linear, left top, left bottom, from(#599bdc), to(#3072b3));
    background-image: -o-linear-gradient(#599bdc, #3072b3);
    background-image: linear-gradient(#599bdc, #3072b3);
    border-color: #518cc6 #518cc6 #2a65a0;
    text-shadow: 0 -1px 0 rgba(0, 0, 0, .25)
}

.user-card .header {
    padding: 3px 0 4px 57px;
    min-height: 48px
}

.user-card .header a {
    color: #707070;
    text-decoration: none
}

.user-card .header a:hover strong {
    text-decoration: underline
}

.user-card img {
    position: absolute;
    top: 0;
    left: 0;
    width: 48px;
    height: 48px;
    background: #fff;
    border-radius: 4px
}

.user-card strong {
    display: block;
    color: #292f33;
    font-size: 16px;
    line-height: 1.6
}

.user-card ul {
    text-transform: uppercase;
    font-size: 12px;
    color: #707070;
    list-style-type: none;
    margin: 0;
    padding: 0;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    zoom: 1
}

.user-card ul:after {
    display: block;
    content: '';
    clear: both
}

.user-card .status a {
    color: #707070;
    text-decoration: none
}

.user-card .status a:hover {
    color: #4183c4
}

.user-card .status li {
    float: left;
    padding: 4px 18px;
    border-left: 1px solid #eee
}

.user-card .status li:first-child {
    border-left: 0;
    padding-left: 0
}

.user-card .footer {
    font-size: 12px;
    font-weight: 700;
    padding: 11px 0 10px;
    color: #646464
}

.user-card .footer a {
    color: #646464
}

.repo-card .header {
    padding: 3px 0 4px 57px
}

.repo-card .avatar,
.repo-card .avatar img {
    position: absolute;
    top: 0;
    left: 0;
    width: 48px;
    height: 48px;
    background: #fff;
    border-radius: 4px
}

.repo-card .header a {
    color: #707070
}

.repo-card .header strong {
    display: block;
    font-size: 18px;
    line-height: 1.4
}

.repo-card .header strong a {
    color: #292f33
}

.repo-card .header sup {
    font-size: 10px;
    margin-left: 3px;
    color: #797979
}

.repo-card .content {
    padding: 6px 0 10px
}

.repo-card .content p {
    margin: 0 5px 0 0;
    font: 18px/24px Georgia, "Times New Roman", Palatino, serif;
    overflow: hidden;
    clear: both;
    word-wrap: break-word
}

.repo-card .footer {
    border-top: 1px solid #eee;
    padding: 8px 0 6px
}

.repo-card .status {
    font-size: 10px;
    padding-right: 10px;
    text-transform: uppercase
}

.repo-card .status strong {
    font-size: 12px;
    padding-right: 5px
}

body,
p,
input,
textarea {
    font-size: 14px;
    word-break: break-word;
}

textarea,
input[type="text"],
input[type="password"],
input[type="datetime"],
input[type="datetime-local"],
input[type="date"],
input[type="month"],
input[type="time"],
input[type="week"],
input[type="number"],
input[type="email"],
input[type="url"],
input[type="search"],
input[type="tel"],
input[type="color"],
.uneditable-input {
    background: hsla(0, 0%, 0%, 0)
}

pre {
    background: #fee9cc;
    border: 1px dashed #ccc;
    line-height: 22px;
}

code {
    padding: 0;
    border: none;
}

p code {
    background: none;
    color: hsl(0, 0%, 50%);
    margin: 0 1px;
    padding: 1px 4px;
    border-radius: 1px;
}

div pre.prettyprint {
    font-size: 14px;
    border-radius: 0px;
    padding: 0 15px;
    border: none;
    margin: 20px -10px;
    border-width: 1px 0px;
    background: #f7f7f7;
    -o-tab-size: 4;
    -moz-tab-size: 4;
    tab-size: 4;
}

form {
    margin-bottom: 0;
}

textarea {
    margin-bottom: 0;
}

input,
textarea {
    background: hsla(0, 0%, 0%, 0);
}



